.children-column{
    text-align: center;
    border:solid 1px white;
    width:50%;
    min-width: 10rem;
    padding:0.4rem;
    margin:0.5rem;
    max-width: 25rem;
    align-items: center;

    p{
        color:gray;
    }

    img{
        max-width:100%;
    }
}

.children-column > h3,h4,h5,h6,p{
    display: flex;
    align-items: center;
    justify-content: center;
    color:rgb(195, 218, 195);
}


.children-column > a{
    color:rgb(118, 150, 118);
    font-weight: 800;
    text-decoration: none;
    transition: all 0.4s;
}

a:hover{
    color:green;
}