.children-content{
    display: flex;
    align-items: center;
    flex-direction: column;
    text-align: center;
    border:solid 1px white;
    width:10%;
    min-width: 4rem;
    padding:0.4rem;
    margin:0.5rem;

    h3,h4,h5,h6,p{
        display: contents;
        align-items: center;
        justify-content: center;
        display: contents;
        color:rgb(195, 218, 195);

    }
}

a{
    color:white;
    font-weight: 800;
    text-decoration: none;
    transition: all 0.4s;
}

a:hover{
    color:green;
}