.presentation{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-items: center;

    h2,h3{
        color:rgb(195, 218, 195);
    }

    hr{
        border-color:green;
    }
}

.principal-presentation{
    display: flex;
    align-items: center;
}

.down-button{
    margin:10%;
}

.down-button:hover{
    cursor:pointer;
}

.down-button > a{
    color: white;
}

.text-box{
    width: 50%;
    text-align: center;
    max-width: 50%;
    display: block;
    flex-direction: column;

}

.container{
    display: flex;
    flex-wrap: wrap;
    width:100%;
    align-items: center;
    justify-content: center;
}