.header-nav{
    display: flex;
    justify-content: center;
}

.nav-list{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 40%;
    list-style: none;
    background-color: rgb(10, 10, 10);
    padding:0.8rem;
    border-radius: 0.8rem;
    position: fixed;
    z-index:10;
}

.nav-item{
    font-size: medium;
    font-weight: 600;
    transition: all 0.3s;
}

.nav-item:hover{
    cursor: pointer;
    color:rgb(10, 156, 10);
}

.selectedItem{
    text-decoration: underline;
    text-decoration-color: green;
    text-decoration-thickness: 0.1rem;
}

.selectedItem:hover{
    text-decoration-color: rgba(70, 137, 196, 0);
}