.button{
    display: block;
    background-color: rgb(8, 8, 8);
    width: 40%;
    padding: 0.1rem;
    justify-self: center;
    margin:0 auto;
    border-radius: 0.5rem;
    margin-top: 0.5rem;
    border:1px rgba(103, 221, 103, 0.356) solid;

    h1,h2,h3,h4,h5,h6,p{
        display: contents;
        font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif
    }
}

.button:hover{
    cursor:pointer;
}

a{
    font-weight: 200;
    text-decoration: none;
}